export default {
	data() {
		return {
			addressData: {
				receivername: '',
				mobile: '',
				addressName: '在地图选择',
				address: '',
				area: '',
				default_: false
			}
		}
	},
	onLoad(option) {
		let title = '新增收货地址';
		if (option.type === 'edit') {
			title = '编辑收货地址'

			this.addressData = JSON.parse(option.data)
		}
		this.manageType = option.type;
		uni.setNavigationBarTitle({
			title
		})
	},
	methods: {
		switchChange(e) {
			//设置为默认地址的方法
			//首先判断当前是不是默认地址，如果是那么就把 其置为非默认
			//如果不是默认地址那么就是把其置为默认地址

			if (e.detail.value) {
				this.addressData.default_ = 1
			} else {
				this.addressData.default_ = 0;
			}
			// console.log("switchChange....")
			// console.log(this.addressData.default_)
			// console.log("switchChange....addressData")
			// console.log(this.addressData)
		},

		//地图选择地址
		chooseLocation() {
			console.log("chooseLocation")
			uni.chooseLocation({
				success: (data) => {
					this.addressData.addressName = data.name;
					this.addressData.address = data.name;
				}
			})
		},

		//提交
		confirm() {
			let data = this.addressData;
			if (!data.receivername) {
				this.$api.msg('请填写收货人姓名');
				return;
			}
			if (!(/^1[3456789]\d{9}$/.test(data.mobile))) {
				this.$api.msg('请输入正确的手机号码');
				return;
			}
			if (!data.address) {
				this.$api.msg('请在地图选择所在位置');
				return;
			}
			if (!data.area) {
				this.$api.msg('请填写门牌号信息');
				return;
			}
			try {
				var c_id = uni.getStorageSync("userInfo").id;
				data.c_id = c_id;
			} catch (e) {

			}

			//console.log("......data........")
			//console.log(data);
			//this.$api.prePage()获取上一页实例，可直接调用上页所有数据和方法，在App.vue定义
			//	this.$api.prePage().refreshList(data, this.manageType);
			var url = (this.manageType == 'edit') ? this.myroot + "updateAddress" : this.myroot + "insertAddress";
			uni.request({
				url: url,
				data: data,
				dataType: "json",
				method: "POST",
				success: (res) => {
					if (res.data.code == 0) {
						this.$api.msg(`地址${this.manageType=='edit' ? '修改': '添加'}成功`);
						setTimeout(() => {
							uni.navigateBack()
						}, 800)
					}
				}
			})
		},
	}
}
